import { defineComponent } from "vue";
import useHomeSlide from "@/hooks/useHomeSlide";
import { IonToolbar } from "@ionic/vue";
import { Swiper, SwiperSlide } from "swiper/vue";
import "swiper/css";
import "@ionic/vue/css/ionic-swiper.css";
export default defineComponent({
  components: {
    IonToolbar,
    Swiper,
    SwiperSlide
  },
  emits: ["clickSlide"],
  props: {
    title: {
      type: String,
      require: true
    },
    category: {
      type: String,
      require: true
    }
  },
  setup(props) {
    const swiperOptions = {
      480: {
        slidesPerView: 2.5
      },
      768: {
        slidesPerView: 3.5
      },
      1024: {
        slidesPerView: 4.5
      }
    };
    const {
      getActiveHomeSlideByType
    } = useHomeSlide();
    const targetSlide = getActiveHomeSlideByType(props.title);
    return {
      swiperOptions,
      targetSlide
    };
  }
});